import React, { useState, useEffect, useMemo, useRef } from "react";

// Kid-friendly Multiplication Trainer — v2.6.1 (bilingual FR/EN - fixed duplicates)
export default function AppPreview() {
  // ===== Persistent Config =====
  const [name, setName] = useState("");
  const [soundEnabled, setSoundEnabled] = useState(true);
  const [enabledTables, setEnabledTables] = useState([]); // [1..10]
  const [answerMode, setAnswerMode] = useState("choice"); // 'choice' | 'input'
  const [goal, setGoal] = useState(10); // 5–20 questions per session
  const [timerEnabled, setTimerEnabled] = useState(false);
  const [timeLimit, setTimeLimit] = useState(10); // seconds 1–10
  const [language, setLanguage] = useState("fr"); // 'fr' | 'en'

  // ===== Game State =====
  const [started, setStarted] = useState(false);
  const [a, setA] = useState(null);
  const [b, setB] = useState(null);
  const [choices, setChoices] = useState([]);
  const [answer, setAnswer] = useState("");
  const [streak, setStreak] = useState(0); // visual progress bar only
  const [feedback, setFeedback] = useState(null); // {type:'ok'|'err', text}
  const [answerLocked, setAnswerLocked] = useState(false); // prevent multiple submissions

  // ===== Session Tracking (continuous mode) =====
  const [qCount, setQCount] = useState(0);
  const [correctCount, setCorrectCount] = useState(0);
  const [timeAccMs, setTimeAccMs] = useState(0);

  // End-of-session UI
  const [showSummary, setShowSummary] = useState(false);
  const [finalSummary, setFinalSummary] = useState(null); // { total, correct, pct, avgS }
  const [celebrate, setCelebrate] = useState(false);

  // Results screen
  const [showResultsView, setShowResultsView] = useState(false);

  // Timer
  const [elapsed, setElapsed] = useState(0); // ms for current question (for bar)
  const timerRef = useRef(null);
  const questionStartRef = useRef(null);

  // Confetti seed
  const [confettiSeed, setConfettiSeed] = useState(0);

  // ===== TRANSLATIONS =====
  const translations = {
    fr: {
      hello: "Bonjour",
      champion: "CHAMPION",
      streak: "Série:",
      chooseTables: "Choisis tes tables, le type de réponse et l'objectif 🎯",
      yourName: "Ton prénom :",
      namePlaceholder: "Entre ton prénom ici 💬",
      sound: "Son :",
      soundOn: "Activé 🎵",
      soundOff: "Muet 🔇",
      answerType: "Type de réponse :",
      multipleChoice: "🎯 Choix multiples",
      manualInput: "✍️ Saisie manuelle",
      goalLabel: "Nombre de questions",
      timerLabel: "⏱ Activer le minuteur",
      timerOn: "ON",
      timerOff: "OFF",
      timePerQuestion: "Temps par question (s)",
      tablesSelected: "Tables sélectionnées:",
      selectAll: "Tout sélectionner",
      deselectAll: "Tout désélectionner",
      startGame: "🚀 Commencer le jeu !",
      myResults: "📈 Mes résultats",
      selectAtLeastOne: "Sélectionne au moins une table pour commencer.",
      selectAtLeastOneShort: "Sélectionne au moins une table.",
      back: "🔙 Retour ⚙️",
      correct: "✅ Bravo !",
      wrong: "❌ Oups!",
      timeUp: "❌ Temps écoulé ⏰",
      resultsTitle: "📈 Mes résultats",
      anonymous: "Anonyme",
      date: "Date",
      score: "Score",
      avgTime: "Temps moyen",
      noResults: "Aucun résultat pour l'instant.",
      deleteResults: "🗑️ Effacer mes résultats",
      home: "🏠 Retour",
      homeShort: "🏠 Accueil",
      congratulations: "Félicitations !",
      tryAgain: "Tu peux faire mieux la prochaine fois !",
      correct_plural: "corrects",
      avgTimeLabel: "Temps moyen :",
      viewResults: "📈 Voir mes résultats",
      confirmDelete: "Effacer tous les résultats pour cet élève ?",
      language: "Langue :",
      timeRemaining: "⏱️ Temps restant",
    },
    en: {
      hello: "Hello",
      champion: "CHAMPION",
      streak: "Streak:",
      chooseTables: "Choose your tables, answer type, and goal 🎯",
      yourName: "Your name:",
      namePlaceholder: "Enter your name here 💬",
      sound: "Sound:",
      soundOn: "On 🎵",
      soundOff: "Muted 🔇",
      answerType: "Answer type:",
      multipleChoice: "🎯 Multiple choice",
      manualInput: "✍️ Manual input",
      goalLabel: "Number of questions",
      timerLabel: "⏱ Enable timer",
      timerOn: "ON",
      timerOff: "OFF",
      timePerQuestion: "Time per question (s)",
      tablesSelected: "Tables selected:",
      selectAll: "Select all",
      deselectAll: "Deselect all",
      startGame: "🚀 Start the game!",
      myResults: "📈 My results",
      selectAtLeastOne: "Select at least one table to start.",
      selectAtLeastOneShort: "Select at least one table.",
      back: "🔙 Back ⚙️",
      correct: "✅ Great job!",
      wrong: "❌ Oops!",
      timeUp: "❌ Time's up ⏰",
      resultsTitle: "📈 My results",
      anonymous: "Anonymous",
      date: "Date",
      score: "Score",
      avgTime: "Avg time",
      noResults: "No results yet.",
      deleteResults: "🗑️ Delete my results",
      home: "🏠 Back",
      homeShort: "🏠 Home",
      congratulations: "Congratulations!",
      tryAgain: "You can do better next time!",
      correct_plural: "correct",
      avgTimeLabel: "Average time:",
      viewResults: "📈 View my results",
      confirmDelete: "Delete all results for this student?",
      language: "Language:",
      timeRemaining: "⏱️ Time remaining",
    }
  };

  const t = translations[language];

  // ===== PUBLIC BASE (works at / or /multitrainer/) =====
  const publicBase = useMemo(() => {
    try {
      const baseEl = document.querySelector('base[href]');
      if (baseEl) {
        let href = baseEl.getAttribute('href') || '/';
        if (!href.endsWith('/')) href += '/';
        return href;
      }
    } catch {}
    try {
      const path = (typeof window !== 'undefined' && window.location && window.location.pathname) || '/';
      const segs = path.split('/').filter(Boolean);
      return segs.length ? `/${segs[0]}/` : '/';
    } catch { return '/'; }
  }, []);

  // ===== Load / Save config =====
  useEffect(() => {
    try {
      const saved = JSON.parse(localStorage.getItem('mt_v2_config') || 'null');
      if (saved) {
        if (Array.isArray(saved.enabledTables)) setEnabledTables(saved.enabledTables);
        if (typeof saved.name === 'string') setName(saved.name);
        if (typeof saved.soundEnabled === 'boolean') setSoundEnabled(saved.soundEnabled);
        if (["choice","input"].includes(saved.answerMode)) setAnswerMode(saved.answerMode);
        if (Number.isFinite(saved.goal)) setGoal(Math.min(20, Math.max(5, saved.goal)));
        if (typeof saved.timerEnabled === 'boolean') setTimerEnabled(saved.timerEnabled);
        if (Number.isFinite(saved.timeLimit)) setTimeLimit(Math.min(10, Math.max(1, saved.timeLimit)));
        if (["fr","en"].includes(saved.language)) setLanguage(saved.language);
      }
    } catch {}
  }, []);

  useEffect(() => {
    const cfg = { name, soundEnabled, enabledTables, answerMode, goal, timerEnabled, timeLimit, language };
    try { localStorage.setItem('mt_v2_config', JSON.stringify(cfg)); } catch {}
  }, [name, soundEnabled, enabledTables, answerMode, goal, timerEnabled, timeLimit, language]);

  // ===== Utils =====
  const randInt = (min, max) => Math.floor(Math.random() * (max - min + 1)) + min;

  const play = (kind) => {
    if (!soundEnabled || typeof Audio === 'undefined') return;
    const file = kind === 'ok'
      ? `${publicBase}sounds/correct.wav`
      : kind === 'clap'
        ? `${publicBase}sounds/clapping.wav`
        : `${publicBase}sounds/wrong.wav`;
    try { new Audio(file).play().catch(() => {}); } catch {}
  };

  function pickWrongAnswers(correct, count = 2) {
    const out = new Set();
    const near = [-5,-3,-2,-1,1,2,3,4,5];
    while (out.size < count) {
      const v = correct + near[randInt(0, near.length - 1)];
      if (v >= 0 && v !== correct) out.add(v);
    }
    return Array.from(out);
  }

  // ===== Timer control =====
  function stopTimer() { if (timerRef.current) { clearInterval(timerRef.current); timerRef.current = null; } }

  function startTimer() {
    stopTimer();
    setElapsed(0);
    questionStartRef.current = performance.now();
    if (!timerEnabled) return;
    const totalMs = timeLimit * 1000;
    const startedAt = performance.now();
    timerRef.current = setInterval(() => {
      const now = performance.now();
      const e = now - startedAt;
      setElapsed(e);
      if (e >= totalMs) {
        // TIMEOUT -> wrong
        stopTimer();
        const qElapsed = now - (questionStartRef.current || now);
        setFeedback({ type: 'err', text: t.timeUp });
        play('no');
        setStreak(0);
        const newQ = qCount + 1;
        const newCorrect = correctCount; // wrong
        const newTime = timeAccMs + qElapsed;
        setQCount(newQ);
        setTimeAccMs(newTime);
        if (newQ >= goal) {
          setTimeout(() => endSession(newQ, newCorrect, newTime), 600);
        } else {
          setTimeout(() => generateQuestion(), 600);
        }
      }
    }, 100);
  }

  // ===== Question generation =====
  function generateQuestion() {
    if (enabledTables.length === 0) {
      setFeedback({ type: 'err', text: t.selectAtLeastOne });
      return;
    }
    const newA = enabledTables[randInt(0, enabledTables.length - 1)];
    const newB = randInt(0, 10);
    setA(newA);
    setB(newB);
    setAnswer("");
    setFeedback(null);
    setAnswerLocked(false); // Unlock for new question

    if (answerMode === 'choice') {
      const correct = newA * newB;
      const wrongs = pickWrongAnswers(correct, 2);
      setChoices([correct, ...wrongs].sort(() => Math.random() - 0.5));
    } else {
      setChoices([]);
    }
    startTimer();
  }

  // ===== Start game =====
  function startGame() {
    if (enabledTables.length === 0) {
      setFeedback({ type: 'err', text: t.selectAtLeastOneShort });
      return;
    }
    setQCount(0); setCorrectCount(0); setTimeAccMs(0);
    setShowSummary(false); setFinalSummary(null);
    setConfettiSeed(0); setCelebrate(false);
    setStreak(0);
    setFeedback(null); // Clear any previous feedback
    setStarted(true);
    generateQuestion();
  }

  // ===== Answer handling =====
  function handleAnswer(val) {
    if (answerLocked) return; // Prevent multiple submissions
    
    const userVal = answerMode === 'choice' ? Number(val) : Number(String(answer).trim());
    if (!Number.isFinite(userVal)) return;
    
    setAnswerLocked(true); // Lock immediately after first valid submission
    
    const correctVal = a * b;
    const good = userVal === correctVal;

    const now = performance.now();
    const qElapsed = now - (questionStartRef.current || now);
    stopTimer();

    if (good) {
      play('ok');
      setStreak(s => s + 1);
      setCorrectCount(c => c + 1);
      setFeedback({ type: 'ok', text: t.correct });
    } else {
      play('no');
      setStreak(0);
      setFeedback({ type: 'err', text: `${t.wrong} ${a} × ${b} = ${correctVal}` });
    }

    const newQ = qCount + 1;
    const newCorrect = correctCount + (good ? 1 : 0);
    const newTime = timeAccMs + qElapsed;
    setQCount(newQ); setTimeAccMs(newTime);

    if (newQ >= goal) {
      setTimeout(() => endSession(newQ, newCorrect, newTime), 600);
    } else {
      setTimeout(() => generateQuestion(), 600);
    }
  }

  // ===== End session (explicit totals) =====
  function endSession(total, correct, timeSumMs) {
    setStarted(false);
    stopTimer();

    const avgSec = (timeSumMs / total) / 1000;
    const scorePct = Math.round((correct / total) * 100);

    // persist locally per student
    saveSession(name || t.anonymous, {
      date: new Date().toISOString(),
      correct,
      total,
      avgTime: Number(avgSec.toFixed(1))
    });

    const fs = { total, correct, pct: scorePct, avgS: Number(avgSec.toFixed(1)) };
    setFinalSummary(fs);

    if (scorePct >= 60) {
      setConfettiSeed(Date.now());
      setCelebrate(true);
      play('clap');
      setTimeout(() => setShowSummary(true), 500);
    } else {
      setCelebrate(false);
      setShowSummary(true);
    }
  }

  // ===== Storage (per student name) =====
  function loadSessions(student) {
    try {
      const raw = localStorage.getItem(`results_${student}`);
      if (!raw) return { sessions: [] };
      const parsed = JSON.parse(raw);
      if (!parsed || !Array.isArray(parsed.sessions)) return { sessions: [] };
      return parsed;
    } catch { return { sessions: [] }; }
  }

  function saveSession(student, entry) {
    const data = loadSessions(student);
    data.sessions.unshift(entry);
    if (data.sessions.length > 50) data.sessions.length = 50;
    try { localStorage.setItem(`results_${student}`, JSON.stringify(data)); } catch {}
  }

  function clearSessions(student) {
    try { localStorage.removeItem(`results_${student}`); } catch {}
  }

  const [sessionsForStudent, setSessionsForStudent] = useState([]);
  
  // Load sessions when relevant state changes
  const loadCurrentSessions = () => {
    setSessionsForStudent(loadSessions(name || t.anonymous).sessions);
  };
  
  useEffect(() => {
    loadCurrentSessions();
  }, [name, showSummary, showResultsView, finalSummary, language]);

  // ===== Derived visuals =====
  const percent = Math.max(0, Math.min(100, Math.round((streak / goal) * 100)));
  const timerPct = timerEnabled ? Math.max(0, Math.min(100, Math.round((elapsed / (timeLimit * 1000)) * 100))) : 0;
  const isTimerLate = timerEnabled && timerPct >= 90;

  // ===== Confetti pieces =====
  const confettiPieces = useMemo(() => {
    if (!celebrate) return [];
    const rnd = (seed) => { const x = Math.sin(seed) * 10000; return x - Math.floor(x); };
    const seed = confettiSeed || 1;
    const theme = [ {h:205,s:90,l:55}, {h:48,s:100,l:55}, {h:320,s:80,l:62} ];
    return Array.from({ length: 32 }, (_, i) => {
      const r1 = rnd(seed + i * 1.1);
      const r2 = rnd(seed + i * 2.3);
      const r3 = rnd(seed + i * 3.7);
      const color = theme[i % theme.length];
      return {
        left: `${Math.round(r1 * 100)}vw`,
        delay: `${(r2 * 0.6).toFixed(2)}s`,
        duration: `${(1.8 + r3 * 1.4).toFixed(2)}s`,
        size: `${6 + Math.round(r1 * 12)}px`,
        rotate: `${Math.round(r2 * 360)}deg`,
        bg: `hsl(${color.h}, ${color.s}%, ${color.l}%)`,
      };
    });
  }, [celebrate, confettiSeed]);

  // ===== Styles =====
  const Styles = () => (
    <style>{`
      @keyframes confetti-fall { 0% { transform: translate3d(0, -10vh, 0) rotate(0deg); opacity: 1; } 100% { transform: translate3d(0, 110vh, 0) rotate(360deg); opacity: 0; } }
      @keyframes timer-pulse { 0%,100% { transform: scale(1); } 50% { transform: scale(1.03); } }
    `}</style>
  );

  // ===== Medals =====
  const medalForPct = (pct) => (pct >= 90 ? '🥇' : pct >= 75 ? '🥈' : pct >= 60 ? '🥉' : '❗');

  // ===== Delete handler (clear + back to main) =====
  const handleDeleteResults = () => {
    if (!window.confirm(t.confirmDelete)) return;
    const studentName = name || t.anonymous;
    clearSessions(studentName);
    loadCurrentSessions(); // Refresh the display
  };

  // ===== Render =====
  return (
    <div className="min-h-screen bg-gradient-to-b from-blue-100 to-pink-100 flex flex-col items-center justify-center p-4 text-gray-800 relative overflow-hidden">
      <Styles />

      <div className="bg-white shadow-2xl rounded-3xl p-6 w-full max-w-md border-4 border-blue-300 relative z-10">
        {/* Header inside card */}
        <div className="flex flex-col items-center mb-4 text-center">
          <h1 className="text-4xl font-extrabold text-blue-700">
            👋 {t.hello} <span className="text-pink-500 uppercase">{name || t.champion}</span>
          </h1>
          {started && (
            <div className="flex items-center justify-center mt-3 gap-3 w-full">
              <span className="text-sm font-medium text-gray-700">{t.streak}</span>
              <div className="flex-1 h-4 bg-gray-200 rounded-full overflow-hidden max-w-xs shadow-inner">
                <div className="h-full transition-all duration-700 ease-out bg-gradient-to-r from-blue-500 via-purple-500 to-pink-500" style={{ width: `${percent}%` }} />
              </div>
              <span className="text-sm font-semibold w-16 text-right">{streak}/{goal}</span>
            </div>
          )}
        </div>

        {/* Screens */}
        {!started && !showResultsView ? (
          <>
            <p className="text-center text-gray-700 mb-4">{t.chooseTables}</p>

            {/* Name input - full width */}
            <div className="mb-3 grid grid-cols-[auto_1fr] gap-3 items-center">
              <label className="font-medium text-left whitespace-nowrap">{t.yourName}</label>
              <input type="text" value={name} onChange={(e) => setName(e.target.value)} className="p-2 border-2 border-blue-200 rounded-xl text-lg w-full" placeholder={t.namePlaceholder} />
            </div>

            {/* Language and Sound toggles - side by side */}
            <div className="mb-3 grid grid-cols-2 gap-3">
              {/* Language toggle */}
              <div className="flex flex-col gap-1">
                <span className="font-medium text-left text-sm">{t.language}</span>
                <button onClick={() => setLanguage(l => l === 'fr' ? 'en' : 'fr')} className="flex items-center justify-around rounded-full border bg-purple-50 px-2 py-2 transition w-full">
                  <span className={`px-3 py-1 rounded-full text-xs font-semibold transition ${language === 'fr' ? 'bg-purple-600 text-white' : 'text-gray-700'}`}>🇫🇷 FR</span>
                  <span className={`px-3 py-1 rounded-full text-xs font-semibold transition ${language === 'en' ? 'bg-purple-600 text-white' : 'text-gray-700'}`}>🇺🇸 EN</span>
                </button>
              </div>

              {/* Sound toggle */}
              <div className="flex flex-col gap-1">
                <span className="font-medium text-left text-sm">{t.sound}</span>
                <button onClick={() => setSoundEnabled(v => !v)} className={`flex items-center justify-around rounded-full border bg-blue-50 px-2 py-2 transition w-full ${soundEnabled ? 'ring-2 ring-blue-300' : ''}`}>
                  <span className={`px-3 py-1 rounded-full text-xs font-semibold transition ${soundEnabled ? 'bg-blue-600 text-white' : 'text-gray-700'}`}>{soundEnabled ? t.soundOn : t.soundOff}</span>
                </button>
              </div>
            </div>

            {/* Answer mode toggle - full width */}
            <div className="mb-3 grid grid-cols-[auto_1fr] gap-3 items-center">
              <span className="font-medium text-left whitespace-nowrap">{t.answerType}</span>
              <button onClick={() => setAnswerMode(m => m === 'choice' ? 'input' : 'choice')} className="flex items-center justify-around rounded-full border bg-yellow-50 px-2 py-2 w-full">
                <span className={`px-2 py-1 rounded-full text-xs font-semibold transition ${answerMode === 'choice' ? 'bg-yellow-400 text-white' : 'text-gray-700'}`}>{t.multipleChoice}</span>
                <span className={`px-2 py-1 rounded-full text-xs font-semibold transition ${answerMode === 'input' ? 'bg-yellow-400 text-white' : 'text-gray-700'}`}>{t.manualInput}</span>
              </button>
            </div>

            {/* Goal slider */}
            <div className="mb-3 grid grid-cols-[auto_1fr] gap-3 items-center">
              <span className="font-medium text-left whitespace-nowrap">{t.goalLabel}</span>
              <div className="flex items-center gap-3 w-full">
                <input type="range" min={5} max={20} step={1} value={goal} onChange={(e) => setGoal(Number(e.target.value))} className="flex-1 accent-blue-600" />
                <span className="text-blue-700 font-bold w-8 text-center">{goal}</span>
              </div>
            </div>

            {/* Timer toggle */}
            <div className="mb-3 grid grid-cols-[auto_1fr] gap-3 items-center">
              <span className="font-medium text-left whitespace-nowrap">{t.timerLabel}</span>
              <button onClick={() => setTimerEnabled(v => !v)} className={`flex items-center justify-around rounded-full border px-3 py-2 w-full ${timerEnabled ? 'bg-blue-50 ring-2 ring-blue-300' : 'bg-gray-50'}`}>
                <span className={`px-4 py-1 rounded-full text-sm font-semibold transition ${timerEnabled ? 'bg-blue-600 text-white' : 'text-gray-700'}`}>{timerEnabled ? t.timerOn : t.timerOff}</span>
              </button>
            </div>

            {/* Timer duration (conditional) */}
            {timerEnabled && (
              <div className="mb-3 grid grid-cols-[auto_1fr] gap-3 items-center">
                <span className="font-medium text-left text-sm whitespace-nowrap">{t.timePerQuestion}</span>
                <div className="flex items-center gap-3 w-full">
                  <input type="range" min={1} max={10} step={1} value={timeLimit} onChange={(e) => setTimeLimit(Number(e.target.value))} className="flex-1 accent-blue-600" />
                  <span className="text-blue-700 font-bold w-8 text-center">{timeLimit}</span>
                </div>
              </div>
            )}

            {/* Table selection */}
            <div className="mb-2">
              <div className="flex items-center justify-between mb-3">
                <span className="font-medium">{t.tablesSelected} {enabledTables.length}</span>
                <div className="flex gap-2">
                  <button onClick={() => setEnabledTables(Array.from({ length: 10 }, (_, i) => i + 1))} className="text-xs px-4 py-2 rounded-lg bg-gradient-to-r from-green-400 to-green-500 hover:from-green-500 hover:to-green-600 text-white font-semibold shadow-sm transition">✅ {t.selectAll}</button>
                  <button onClick={() => setEnabledTables([])} className="text-xs px-4 py-2 rounded-lg bg-gradient-to-r from-red-400 to-red-500 hover:from-red-500 hover:to-red-600 text-white font-semibold shadow-sm transition">❌ {t.deselectAll}</button>
                </div>
              </div>
            </div>

            <div className="grid grid-cols-5 gap-2 mb-5">
              {Array.from({ length: 10 }, (_, i) => i + 1).map((num) => (
                <button key={num} onClick={() => setEnabledTables(prev => prev.includes(num) ? prev.filter(n => n !== num) : [...prev, num])} className={`rounded-lg py-2 text-lg font-bold shadow transition-all duration-200 ${enabledTables.includes(num) ? 'bg-gradient-to-r from-blue-500 to-teal-400 text-white' : 'bg-blue-100 hover:bg-blue-200'}`}>{num}</button>
              ))}
            </div>

            <div className="flex items-center justify-between gap-3">
              <button onClick={startGame} className="flex-1 bg-gradient-to-r from-pink-500 to-yellow-400 hover:from-pink-600 hover:to-yellow-500 text-white font-bold py-3 px-6 rounded-full shadow-lg">{t.startGame}</button>
              <button onClick={() => setShowResultsView(true)} className="bg-white border-2 border-blue-300 text-blue-700 font-bold py-3 px-4 rounded-full shadow">{t.myResults}</button>
            </div>

            {feedback && feedback.type === 'err' && <p className={`mt-3 text-center text-red-600`}>{feedback.text}</p>}
          </>
        ) : started ? (
          <div className="text-center">
            <h2 className="text-3xl font-extrabold mb-4 text-blue-700">{a} × {b} = ?</h2>

            {/* Timer bar (countdown style with urgency) */}
            {timerEnabled && (
              <div className="mb-4">
                <div className="flex items-center justify-between mb-1">
                  <span className="text-xs font-medium text-gray-600">{t.timeRemaining}</span>
                  <span className={`text-xs font-bold ${isTimerLate ? 'text-red-600' : 'text-gray-700'}`}>
                    {Math.max(0, Math.ceil((timeLimit * 1000 - elapsed) / 1000))}s
                  </span>
                </div>
                <div className={`h-2 w-full bg-gray-200 rounded-full overflow-hidden border border-gray-300`} style={{ animation: isTimerLate ? 'timer-pulse 0.8s ease-in-out infinite' : 'none' }}>
                  <div 
                    className="h-full transition-[width] duration-100 ease-linear"
                    style={{ 
                      width: `${100 - timerPct}%`,
                      background: timerPct < 50 ? '#10b981' : timerPct < 75 ? '#f59e0b' : '#ef4444'
                    }} 
                  />
                </div>
              </div>
            )}

            {answerMode === 'choice' ? (
              <div className="grid grid-cols-3 gap-3">
                {choices.map((c, i) => (
                  <button key={i} onClick={() => handleAnswer(c)} className="bg-gradient-to-br from-yellow-100 via-pink-100 to-green-100 py-3 rounded-xl text-2xl font-bold shadow hover:scale-105 transition">{c}</button>
                ))}
              </div>
            ) : (
              <div className="mt-2 flex items-center justify-center gap-2">
                <input type="number" value={answer} onChange={(e) => setAnswer(e.target.value.replace(/[^0-9]/g, ''))} onKeyDown={(e) => e.key === 'Enter' && handleAnswer(answer)} className="p-3 border-2 border-blue-200 rounded-xl w-28 text-center text-2xl bg-blue-50" autoFocus />
                <button onClick={() => handleAnswer(answer)} className="bg-gradient-to-r from-blue-500 to-teal-400 hover:from-blue-600 hover:to-teal-500 text-white py-2 px-5 rounded-xl text-lg shadow">OK</button>
              </div>
            )}

            {/* Fixed-height feedback area to avoid layout shift */}
            <div className="mt-4 h-6 flex items-center justify-center">{feedback && (<p className={`text-lg ${feedback.type === 'err' ? 'text-red-600' : 'text-green-600'}`}>{feedback.text}</p>)}</div>

            <button onClick={() => { setStarted(false); setFeedback(null); stopTimer(); }} className="mt-6 bg-gray-200 hover:bg-gray-300 text-gray-800 py-2 px-4 rounded-md shadow">{t.back}</button>
          </div>
        ) : showResultsView ? (
          <div>
            <h2 className="text-2xl font-extrabold text-blue-700 mb-3">{t.resultsTitle} ({name || t.anonymous})</h2>
            <div className="overflow-hidden rounded-xl border border-blue-200">
              <table className="w-full text-left text-sm">
                <thead className="bg-blue-50">
                  <tr><th className="p-2">{t.date}</th><th className="p-2">{t.score}</th><th className="p-2">{t.avgTime}</th></tr>
                </thead>
                <tbody>
                  {sessionsForStudent.length === 0 ? (
                    <tr><td className="p-3" colSpan={3}>{t.noResults}</td></tr>
                  ) : (
                    sessionsForStudent.map((s, idx) => {
                      const pct = Math.round((s.correct / s.total) * 100);
                      return (
                        <tr key={idx} className={idx % 2 ? 'bg-white' : 'bg-blue-50/40'}>
                          <td className="p-2">{new Date(s.date).toLocaleString()}</td>
                          <td className="p-2 font-semibold">{pct}% {medalForPct(pct)}</td>
                          <td className="p-2">{s.avgTime}s</td>
                        </tr>
                      );
                    })
                  )}
                </tbody>
              </table>
            </div>
            <div className="flex gap-3 mt-4">
              <button onClick={handleDeleteResults} className="bg-red-500 hover:bg-red-600 text-white py-2 px-4 rounded-xl">{t.deleteResults}</button>
              <button onClick={() => setShowResultsView(false)} className="bg-gray-200 hover:bg-gray-300 text-gray-800 py-2 px-4 rounded-xl">{t.home}</button>
            </div>
          </div>
        ) : (
          <div className="text-center">
            <button onClick={() => setShowResultsView(true)} className="bg-white border-2 border-blue-300 text-blue-700 font-bold py-3 px-4 rounded-full shadow">{t.myResults}</button>
          </div>
        )}
      </div>

      {/* Confetti overlay */}
      {celebrate && (
        <div className="pointer-events-none fixed inset-0 z-40">
          {confettiPieces.map((p, idx) => (
            <div key={idx} className="absolute will-change-transform" style={{ left: p.left, top: '-10vh', width: p.size, height: p.size, background: p.bg, transform: `rotate(${p.rotate})`, borderRadius: idx % 3 === 0 ? '2px' : '50%' }}>
              <div style={{ width: '100%', height: '100%', animation: `confetti-fall ${p.duration} cubic-bezier(0.33, 0, 0.67, 1) ${p.delay} both` }} />
            </div>
          ))}
        </div>
      )}

      {/* Summary modal */}
      {showSummary && (
        <div className="fixed inset-0 bg-black/30 backdrop-blur-[1px] flex items-center justify-center z-50">
          <div className="bg-white shadow-2xl rounded-3xl p-8 text-center max-w-md">
            <div className="text-5xl mb-2">{(finalSummary?.pct ?? 0) >= 60 ? '🎉' : '💪'}</div>
            <h3 className="text-2xl font-extrabold text-blue-600 mb-2">{(finalSummary?.pct ?? 0) >= 60 ? t.congratulations : t.tryAgain}</h3>
            <p className="mb-1 font-semibold">{name || t.anonymous} — {finalSummary?.correct}/{finalSummary?.total} {t.correct_plural} ({finalSummary?.pct}%) {finalSummary ? medalForPct(finalSummary.pct) : ''}</p>
            <p className="mb-4">{t.avgTimeLabel} {finalSummary?.avgS}s</p>
            <div className="flex gap-3 justify-center">
              <button onClick={() => { setShowSummary(false); setCelebrate(false); setFeedback(null); }} className="bg-gray-200 hover:bg-gray-300 text-gray-800 py-2 px-4 rounded-xl">{t.homeShort}</button>
              <button onClick={() => { setShowSummary(false); setCelebrate(false); setShowResultsView(true); }} className="bg-blue-600 hover:bg-blue-700 text-white py-2 px-4 rounded-xl">{t.viewResults}</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}